.description {
  padding: 186px 0;

  @include vp-1023 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 65px 0 291px;
  }

  & p {
    margin: 0;
  }

  & h2 {
    font-weight: 600;
    font-size: 28px;
    line-height: 42px;
    margin: 0 0 40px 0;

    @include vp-1023 {
      font-size: 26px;
      line-height: 36px;
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 34px;
    }
  }
}

.description .container {
  position: relative;
}

.description__content {
  display: flex;
  flex-direction: column;
  max-width: 512px;

  @include vp-1023 {
    max-width: 324px;
  }

  @include vp-767 {
    max-width: 100%;
  }

  &::before {
    content: "";
    position: absolute;

    @include vp-900 {
      // max-width: 560px;
      // max-height: 301px;
      width: 40vw;
      height: 22vw;
      max-width: 560px;
      max-height: 301px;
      background: url("../img/description/penguins-desk.jpg") no-repeat top right/contain;

      @include retina {
        background: url("../img/description/penguins-desk@2x.jpg") no-repeat top right/contain;
      }
    }

    // @include vp-1160 {
    //   width: 560px;
    //   height: 301px;
      // background-size: 100%;

      // @include retina {
      //   background-size: 100%;
      // }
    // }

    @include vp-768 {
      top: 0;
      right: 40px;
      width: 40vw;
      height: 22vw;

      background: url("../img/description/penguins-tab.jpg") no-repeat top right/contain;

      @include retina {
        background: url("../img/description/penguins-tab@2x.jpg") no-repeat top right/contain;
      }
    }

    @include vp-767 {
      bottom: -206px;
      left: 50%;
      transform: translateX(-145px);
      // max-width: 350px;
      // max-height: 189px;
      // width: 70vw;
      // height: 40vw;
      width: 290px;
      height: 155px;
      background: url("../img/description/penguins-mob.jpg") no-repeat top right/contain;

      @include retina {
        background: url("../img/description/penguins-mob@2x.jpg") no-repeat top right/contain;
      }
    }

  }
}
